main{
  min-height: 80vh;
}
/* @tailwind base;
@tailwind components;
@tailwind utilities; */

h1{
  font-size: 1.8rem;
  padding:1rem 0;
}
h2{
  font-size: 1.4rem;
  padding:0.5rem 0;
}
h3{
  padding:1rem 0;
}
.card-link {
  text-decoration: none;
}

.card-link:hover {
  text-decoration: underline;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
}

.mySwiper .swiper-button-next,
.mySwiper .swiper-button-prev {
  color: black; 
}

.mySwiper .swiper-pagination-bullet-active {
  background-color: black;
}
